:root {
  --font-inter: 'Inter', system-ui, -apple-system, sans-serif;
  --font-mono: 'JetBrains Mono', 'Fira Code', monospace;
  
  /* Futuristic Neon Color Palette */
  --primary-blue: #00a8ff;
  --primary-blue-hover: #0088cc;
  --accent-cyan: #00f5ff;
  --accent-purple: #a855f7;
  --accent-pink: #ff00ff;
  --accent-green: #00ff88;
  --accent-orange: #ff6b35;
  
  /* Deep space surfaces */
  --surface-dark: #030308;
  --surface-card: rgba(15, 15, 25, 0.85);
  --surface-hover: rgba(30, 30, 50, 0.9);
  --surface-elevated: rgba(20, 20, 35, 0.95);
  
  /* Text with holographic feel */
  --text-primary: #ffffff;
  --text-secondary: #b4bcd0;
  --text-muted: #5a6080;
  
  /* Borders with subtle neon glow */
  --border-subtle: rgba(0, 245, 255, 0.1);
  --border-accent: rgba(0, 245, 255, 0.25);
  --border-neon: rgba(168, 85, 247, 0.4);
  
  /* Neon glow effects */
  --glow-blue: rgba(0, 168, 255, 0.4);
  --glow-cyan: rgba(0, 245, 255, 0.35);
  --glow-purple: rgba(168, 85, 247, 0.4);
  --glow-pink: rgba(255, 0, 255, 0.3);
  --glow-green: rgba(0, 255, 136, 0.35);
  
  /* Scanner/scan line effect */
  --scanline-opacity: 0.03;
}

/* Enhanced scrollbar styling - futuristic */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: rgba(10, 10, 20, 0.8);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, var(--accent-cyan), var(--accent-purple));
  border-radius: 3px;
  box-shadow: 0 0 10px var(--glow-cyan);
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, var(--accent-purple), var(--accent-pink));
  box-shadow: 0 0 15px var(--glow-purple);
}

/* Firefox scrollbar */
* {
  scrollbar-width: thin;
  scrollbar-color: var(--accent-cyan) rgba(10, 10, 20, 0.8);
}

/* Futuristic Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
    filter: blur(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
    filter: blur(0);
  }
}

@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 5px var(--glow-cyan), 0 0 10px var(--glow-cyan);
  }
  50% {
    box-shadow: 0 0 20px var(--glow-cyan), 0 0 40px var(--glow-cyan), 0 0 60px var(--glow-cyan);
  }
}

@keyframes live-pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.8;
  }
}

@keyframes holographic {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes border-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes scan-line {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(100vh);
  }
}

@keyframes data-stream {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%;
  }
}

@keyframes glitch {
  0%, 90%, 100% {
    transform: translate(0);
    filter: none;
  }
  92% {
    transform: translate(-2px, 1px);
    filter: hue-rotate(90deg);
  }
  94% {
    transform: translate(2px, -1px);
    filter: hue-rotate(-90deg);
  }
  96% {
    transform: translate(-1px, 2px);
    filter: hue-rotate(180deg);
  }
  98% {
    transform: translate(1px, -2px);
    filter: none;
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

@keyframes audio-wave {
  0%, 100% {
    height: 4px;
  }
  50% {
    height: 20px;
  }
}

.animate-fade-in-up {
  animation: fadeInUp 0.6s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.animate-fade-in-scale {
  animation: fadeInScale 0.4s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}

.animate-holographic {
  animation: holographic 3s ease infinite;
}

.animate-float {
  animation: float 3s ease-in-out infinite;
}

.animate-glitch {
  animation: glitch 5s infinite;
}

.animate-shimmer {
  background: linear-gradient(
    90deg, 
    transparent 0%, 
    rgba(255, 255, 255, 0.1) 50%, 
    transparent 100%
  );
  background-size: 200% 100%;
  animation: shimmer 2s infinite;
}

/* Loading states - futuristic */
.skeleton {
  background: linear-gradient(
    90deg, 
    rgba(20, 20, 35, 0.8) 25%, 
    rgba(0, 245, 255, 0.1) 50%, 
    rgba(20, 20, 35, 0.8) 75%
  );
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
  border-radius: 4px;
}

/* Neon loading spinner */
.neon-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid transparent;
  border-top-color: var(--accent-cyan);
  border-right-color: var(--accent-purple);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  box-shadow: 
    0 0 10px var(--glow-cyan),
    inset 0 0 10px var(--glow-purple);
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Audio bars animation */
.audio-bars {
  display: flex;
  gap: 2px;
  align-items: flex-end;
  height: 20px;
}

.audio-bar {
  width: 3px;
  background: linear-gradient(to top, var(--accent-cyan), var(--accent-purple));
  border-radius: 2px;
  animation: audio-wave 0.8s ease-in-out infinite;
}

.audio-bar:nth-child(1) { animation-delay: 0s; }
.audio-bar:nth-child(2) { animation-delay: 0.1s; }
.audio-bar:nth-child(3) { animation-delay: 0.2s; }
.audio-bar:nth-child(4) { animation-delay: 0.3s; }
.audio-bar:nth-child(5) { animation-delay: 0.4s; }

/* Particle effect background */
.particles {
  position: fixed;
  inset: 0;
  overflow: hidden;
  pointer-events: none;
  z-index: -1;
}

.particle {
  position: absolute;
  width: 2px;
  height: 2px;
  background: var(--accent-cyan);
  border-radius: 50%;
  opacity: 0.5;
  animation: float 10s infinite ease-in-out;
}

/* Cyber grid background */
.cyber-grid {
  position: fixed;
  inset: 0;
  background-image: 
    linear-gradient(rgba(0, 245, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 245, 255, 0.03) 1px, transparent 1px);
  background-size: 50px 50px;
  pointer-events: none;
  z-index: -1;
  perspective: 1000px;
  transform: rotateX(60deg) translateY(-50%);
  transform-origin: center bottom;
}

/* Utility classes for futuristic effects */
.neon-border {
  border: 1px solid var(--accent-cyan);
  box-shadow: 
    0 0 5px var(--glow-cyan),
    inset 0 0 5px var(--glow-cyan);
}

.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.4);
}

/* Data terminal effect */
.terminal-text {
  font-family: var(--font-mono);
  color: var(--accent-green);
  text-shadow: 0 0 5px var(--glow-green);
}

/* Status indicators */
.status-online {
  color: var(--accent-green);
  text-shadow: 0 0 10px var(--glow-green);
}

.status-offline {
  color: #ff4444;
  text-shadow: 0 0 10px rgba(255, 68, 68, 0.5);
}

.status-loading {
  color: var(--accent-orange);
  text-shadow: 0 0 10px rgba(255, 107, 53, 0.5);
